.container {
    max-width: 1325px;
    margin: auto;

    @media #{$smlg-device} {
        padding: 0 20px;
    }

    @media #{$small-mobile} {
        padding: 0 15px;
    }
}

.container-2 {
    max-width: 1600px;
    margin: auto;

    @media #{$smlg-device} {
        padding: 0 20px;
    }
}

.title-wrapper-center-subtitle {
    text-align: center;
    max-width: 990px;
    margin: auto;

    span {
        display: block;
        background: var(--color-primary);
        padding: 3px 27px;
        max-width: max-content;
        margin: auto;
        border-radius: 33px;
        color: #fff;
        text-transform: uppercase;
        margin-bottom: 20px;
    }

    .title {
        font-size: 70px;

        @media #{$mdsm-layout} {
            font-size: 54px;
            line-height: 1.3;
        }

        @media #{$sm-layout} {
            font-size: 36px;
        }
    }

    p.post-title {
        font-size: 18px;
        max-width: 800px;
        margin: auto;
        margin-bottom: 0;
    }
}